.header__toggle {
  width: 40px;
  height: 20px;
  z-index: 0;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border: none;
  border-radius: 19px;
  margin: 0;
  padding: 0;
  transition-duration: .3s;
  display: inline-block;
  position: relative;
}

.header__toggle:after {
  content: "";
  height: 18px;
  width: 20px;
  z-index: 1;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 17px;
  transition-duration: .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

